.slideshow {
    height: 200px;
    max-width: 800px;
    margin: 0 auto;
    position: relative;
    overflow: hidden;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

.slideshow > div {
    height: 200px;
    width: 2526px;
    /* background: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/3/collage.jpg); */
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  .slide-text {
  -webkit-animation: moveSlideshow 12s linear infinite;
          animation: moveSlideshow 12s linear infinite;
}
.aslide-container {
    display: flex;
    -webkit-animation: slideAnimation 10s linear infinite;
            animation: slideAnimation 10s linear infinite; /* Ajusta el tiempo seg√∫n tus necesidades */
}

.aslide-text {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    -webkit-animation: slideAnimation 10s infinite;
            animation: slideAnimation 10s infinite; /* Ajusta el tiempo seg√∫n tus necesidades */
}

@-webkit-keyframes aslideAnimation {
    0% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    100% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%); /* Ajusta el porcentaje seg√∫n la cantidad de textos */
    }
}

@keyframes aslideAnimation {
    0% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    100% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%); /* Ajusta el porcentaje seg√∫n la cantidad de textos */
    }
} 




@-webkit-keyframes moveSlideshow {
    100% { 
      -webkit-transform: translateX(-66.6666%); 
              transform: translateX(-66.6666%);  
    }
  } 




@keyframes moveSlideshow {
    100% { 
      -webkit-transform: translateX(-66.6666%); 
              transform: translateX(-66.6666%);  
    }
  }



  
